import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import React, { useState, useEffect } from "react";
import firebaseConfig from "./config";
import firebase from "firebase/compat/app";
import { getAnalytics } from "firebase/analytics";
import "./styles/App.css";
import img from "./assets/images/lion.png";

import InfoCard from "./components/IndexCard";

const app = firebase.initializeApp(firebaseConfig);
getAnalytics(app);
function Home() {
  //example time 'December 17, 1995 09:52:00'
  const [date, setDate] = useState(new Date().toLocaleDateString());
  const [logo, setLogo] = useState("");
  const [weather, setWeather] = useState({});
  const [time, setTime] = useState("Loading...");
  const [period, setPeriod] = useState("Loading...");
  const [timeleft, setTimeleft] = useState("Loading...");
  const [selectedOption1, setSelectedOption1] = useState("Regular");
  const [selectedOption2, setSelectedOption2] = useState("Quote");
  const [meme, setMeme] = useState("Loading...");
  const [quote, setQuote] = useState("Loading...");

  const [endOf, setEndOf] = useState([]);
  const [startOf, setStartOf] = useState([]);

  useEffect(() => {
    getWeather();
    getLogo();
  }, []);
  useEffect(() => {
    setDate(new Date().toLocaleDateString());
    let interval = setInterval(() => {
      setTime(new Date().toLocaleTimeString());
    }, 1000);
    return () => clearInterval(interval);
  }, []);
  useEffect(() => {
    checktime();
    // eslint-disable-next-line
  }, [time]);
  useEffect(() => {
    const startMin = convertSchedule([
      "7:20",
      "8:11",
      "9:02",
      "9:56",
      "10:47",
      "12:12",
      "13:03",
      "13:54",
    ]);
    const endMin = convertSchedule([
      "8:06",
      "8:57",
      "9:51",
      "10:42",
      "12:07",
      "12:58",
      "13:49",
      "14:40",
    ]);

    const startActMin = convertSchedule([
      "7:20",
      "8:06",
      "8:52",
      "9:43",
      "10:29",
      "11:57",
      "12:43",
      "13:24",
      "14:10",
    ]);
    const endActMin = convertSchedule([
      "8:01",
      "8:47",
      "9:38",
      "10:24",
      "11:49",
      "12:38",
      "13:24",
      "14:10",
      "14:40",
    ]); //need to do this
    if (selectedOption2 === "Memes") {
      getMeme();
    } else if (selectedOption2 === "Quote") {
      getQuote();
    }

    if (selectedOption1 === "Regular") {
      setEndOf(endMin);
      setStartOf(startMin);
    } else if (selectedOption1 === "Activity") {
      setEndOf(endActMin);
      setStartOf(startActMin);
    }
  }, [selectedOption1, selectedOption2]);

  const getLogo = async () => {
    await fetch(
      `https://MemeApi.neoprint777.repl.co/Logos?number=${Math.floor(
        Math.random() * 12
      )}`
    ).then((data) => setLogo(data));
  };

  const getMeme = async () => {
    await fetch(
      `https://MemeApi.neoprint777.repl.co/Memes?number=${Math.floor(
        Math.random() * 16
      )}`
    ).then((data) => setMeme(data));
  };

  const getQuote = async () => {
    await fetch("https://type.fit/api/quotes")
      .then((response) => response.json())
      .then((data) => setQuote(data[Math.floor(Math.random() * data.length)]));
  };

  const getWeather = async () => {
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=Conroe,Texas&units=imperial&appid=${process.env.REACT_APP_W_API_KEY}`
    )
      .then((res) => res.json())
      .then((data) => setWeather(data));
  };

  function convertSchedule(list) {
    return list.map(
      (min) => parseInt(min.split(":")[0] * 60) + parseInt(min.split(":")[1])
    );
  }
  const checktime = () => {
    //gets period

    let total = new Date().getHours() * 60 + new Date().getMinutes();
    if (total >= startOf[0] && total <= endOf[endOf.length - 1]) {
      if (total >= startOf[0] && total <= endOf[0]) {
        setPeriod(1);
      } else if (total >= startOf[1] && total <= endOf[1]) {
        setPeriod(2);
      } else if (total >= startOf[2] && total <= endOf[2]) {
        setPeriod(3);
      } else if (total >= startOf[3] && total <= endOf[3]) {
        setPeriod(4);
      } else if (total >= startOf[4] && total <= endOf[4]) {
        setPeriod(5);
      } else if (total >= startOf[5] && total <= endOf[5]) {
        setPeriod(6);
      } else if (total >= startOf[6] && total <= endOf[6]) {
        setPeriod(7);
      } else if (total >= startOf[7] && total <= endOf[7]) {
        setPeriod(8);
      } else if (startOf.length === 9 && endOf === 9) {
        if (total >= startOf[8] && total <= endOf[8]) {
          setPeriod("Activity");
        }
      } else {
        setPeriod("In between bells");
      }
    }
    //time left

    if (total >= startOf[0] && total <= endOf[endOf.length - 1]) {
      if (period === 1) {
        setTimeleft(`${endOf[0] - total} min`);
      } else if (period !== "In between bells") {
        setTimeleft(`${endOf[period - 1] - total} min`);
      } else {
        setTimeleft("You made it on time");
      }
    }
  };
  return (
    <React.Fragment>
      <div className="navy page-container bg-fixed">
        <div className="content-wrap">
          <div className="border-full gold px-2 pb-3 rounded-3xl text-md font-light ">
            <div className="flex justify-center ">
              <button
                className={`px-32 text-center text-3xl font-bold bg-white rounded-3xl p-3 mt-5 `}
              >
                Lake Creek
              </button>
            </div>
            <div className="flex justify-center mt-3">
              <select
                className={`rounded-3xl p-2 bg-white mr-3`}
                value={selectedOption1}
                onChange={(e) => setSelectedOption1(e.target.value)}
              >
                <option value="Regular">Regular</option>
                <option value="Activity">Activity</option>
              </select>
              <select
                className={`rounded-3xl p-2 bg-white mr-3`}
                value={selectedOption2}
                onChange={(e) => setSelectedOption2(e.target.value)}
              >
                <option value="Quote">Quote</option>
                <option value="Memes">Memes</option>
              </select>
            </div>
          </div>
          <div
            style={{
              backgroundImage: `url(${img})`,
              backgroundRepeat: "no-repeat",
              backgroundPosition: "center",
              backgroundSize: "350px",
              paddingBottom: "2rem",
            }}
            alt="Lake Creek"
          >
            <div
              className={`grid gap-x-3 gap-y-4 grid-cols-3 place-items-center mt-3`}
            >
              <InfoCard title={`Time`} text={time} />
              <InfoCard
                title={`Weather`}
                text={
                  weather.main
                    ? `${Math.round(weather.main.temp)} Â°F`
                    : `loading`
                }
                image={
                  weather.weather
                    ? `http://openweathermap.org/img/w/${weather.weather[0].icon}.png`
                    : 0
                }
              />
              <InfoCard title={`Date`} text={date} />
              <InfoCard title={`Period`} text={period} />
              {selectedOption2 === "Quote" ? (
                <InfoCard
                  title={`Quote`}
                  text={`"${quote.text}" -${quote.author || "unknown"}`}
                  size="text-xs"
                />
              ) : (
                <InfoCard title="Memes" image={`${meme.url}`} />
              )}
              <InfoCard title={`Class Ends`} text={timeleft} />
            </div>
          </div>
        </div>
        <footer className="flex justify-center mb-auto gold p-3 font-bold text-white border-t-8 border-white">
          <img className="w-12 rounded-full" src={logo.url}></img>
          <p className='self-center ml-3'>Made by NP3D</p>
        </footer>
      </div>
    </React.Fragment>
  );
}

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/timer">
          <Home />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
